*{
 padding: 0;
 margin: 0;
 font-family: "Jost",sans-serif;
}
body{
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height:100vh;
    width:100vw;
    font-family: "Jost",sans-serif;
}
html{
    font-size: 100%;
    height: 100%;
    background: #eee;
}
.box{
    width:20vw;
    height:60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0,0,0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate(0,0,0);
    min-width: 20rem;
    min-height: 30rem;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top:120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}
.wave.three{
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave.two{
    animation: drift 7000ms infinite linear;
    opacity: 0.1;
    z-index: 0.1;
  
}
.box::after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
}
@keyframes drift{
    from{
        transform: rotate(0deg);
    }
    from{
        transform: rotate(360deg);
    }
}
.info{
  position: absolute;
   bottom:6rem;
   width: 100%;
   height: 45%;
   z-index: 4;
}
.location{
     margin-bottom: 2rem;
    text-align: center;
    bottom: 120%;
    font-weight: 800;
    font-size: 3rem;
    font-weight: 600;
}
.fa-street-view{
    animation: rotates 3s linear infinte alternate;
}
@keyframes rotates{
    from{
        transform: translateX(-0.5rem);
    }
    to{
        transform: translateX(0.5rem);
    }
}

.temp{
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}
.tempmin_max{
    text-align: center;
    margin-top: 0.2rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}


@media(max-width:600px){
    .box{
        width: 90vw;
        height: 80vh;
    }
    .wave{
        top:85%;
    }
    
    .info{
        font-size: 1.5rem;
    }
}
@media(max-width:500px){
    .box{
        height: 80vh;
    }
    .wave{
        top:115%;
    }
}
body > span{
    width: 100vw;
    text-align: center;
    color: grey;
}
.inputData{
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputField{
    width: 60%;
    margin: auto;
    border-radius: 2rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.5rem;
}
.errorMsg{
    text-align: center;
    font-size: 2rem;
    margin: 2rem auto;
}
